<template>
    <div class="section mx-6" id="maincontainer">
        <h1 class="title">Video list</h1>

        <!-- <button class="button" v-on:click="allInformation">fetch all</button> -->

        <div v-for="(vid, vindex) in this.Videos" :key="vindex">
            <hr />
            <h6 class="subtitle">{{ vid.filename }}</h6>
            <div class="columns">
                <div class="column">
                    <button
                        class="button is-info is-light"
                        v-on:click="addClip(vindex, -1)"
                        style="width: 100%"
                    >
                        add clip here
                    </button>
                </div>
            </div>
            <div>
                <div v-for="(clip, cindex) in vid.mClips" :key="cindex">
                    <div
                        class="box"
                        v-bind:class="'stripe' + (vindex % 2)"
                        :id="clip.id"
                    >
                        <div class="columns">
                            <div class="column">
                                <input
                                    class="input"
                                    type="number"
                                    id="bH${vindex}_${cindex}"
                                    v-model="vid.mClips[cindex].bH"
                                />
                            </div>
                            <div class="column my-2">
                                <label for="">hour</label>
                            </div>
                            <div class="column">
                                <input
                                    class="input"
                                    type="number"
                                    v-model="vid.mClips[cindex].bM"
                                />
                            </div>
                            <div class="column my-2">
                                <label for="">min</label>
                            </div>
                            <div class="column">
                                <input
                                    class="input"
                                    type="number"
                                    v-model="vid.mClips[cindex].bS"
                                />
                            </div>
                            <div class="column my-2">
                                <label for="">sec</label>
                            </div>
                        </div>

                        <div class="columns">
                            <div class="column">
                                <input
                                    class="input"
                                    type="text"
                                    v-model="vid.mClips[cindex].title"
                                />
                            </div>
                            <div class="column">
                                <input
                                    class="input"
                                    type="datetime-local"
                                    v-model="vid.mClips[cindex].datetime"
                                />
                            </div>
                            <div class="column">
                                <input
                                    class="input"
                                    type="text"
                                    v-model="vid.mClips[cindex].place"
                                />
                            </div>

                            <div class="column">
                                <button
                                    class="button is-danger"
                                    v-on:click="removeClip(vindex, cindex)"
                                >
                                    remove
                                </button>
                            </div>

                            <div class="column">
                                <button
                                    class="button is-info"
                                    v-on:click="updateClip(vindex, cindex)"
                                >
                                    update
                                </button>
                            </div>
                        </div>

                        <div class="columns">
                            <div class="column">
                                <input
                                    class="input"
                                    type="number"
                                    id="bH${vindex}_${cindex}"
                                    v-model="vid.mClips[cindex].eH"
                                />
                            </div>
                            <div class="column my-2">
                                <label for="">hour</label>
                            </div>
                            <div class="column">
                                <input
                                    class="input"
                                    type="number"
                                    v-model="vid.mClips[cindex].eM"
                                />
                            </div>
                            <div class="column my-2">
                                <label for="">min</label>
                            </div>
                            <div class="column">
                                <input
                                    class="input"
                                    type="number"
                                    v-model="vid.mClips[cindex].eS"
                                />
                            </div>
                            <div class="column my-2">
                                <label for="">sec</label>
                            </div>
                        </div>
                    </div>

                    <div class="columns pb-5">
                        <div class="column">
                            <button
                                class="button is-info is-light"
                                v-on:click="addClip(vindex, cindex)"
                                style="width: 100%"
                            >
                                add clip here
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    name: "VideoList",
    props: {
        msg: String,
    },

    data: function () {
        return {
            Videos: [],
        };
    },

    methods: {
        allInformation: function () {
            axios
                .get("http://localhost:8000/videos/")
                .then((response) => {
                    this.Videos = response.data;

                    for (let i in this.Videos) {
                        this.$set(this.Videos[i], "mClips", []);
                    }

                    axios
                        .get("http://localhost:8000/clips/")
                        .then((response) => {
                            this.Clips = response.data;

                            for (let c of this.Clips) {
                                c["elapsed"] = 3600 * c.bH + 60 * c.bM + c.bS;
                                const cs = c.source.split("/");
                                let vIndex = parseInt(cs[cs.length - 2]);
                                let i = 0;
                                for (i in this.Videos) {
                                    if (this.Videos[i].id == vIndex) {
                                        break;
                                    }
                                }
                                this.Videos[i]["mClips"].push(c);
                            }

                            for (let i in this.Videos) {
                                this.Videos[i].mClips.sort((a, b) => {
                                    if (a.elapsed < b.elapsed) return -1;
                                    if (a.elapsed > b.elapsed) return 1;
                                    else return 0;
                                });
                            }
                        })
                        .catch(function (error) {
                            console.log(error);
                        });
                })
                .catch(function (error) {
                    console.log(error);
                    alert("failed to connect with vclip server...");
                });
        },

        addClip: function (VideoIndex, ClipIndex) {
            let clipData;
            let orig;
            if (ClipIndex != -1) {
                orig = this.Videos[VideoIndex].mClips[ClipIndex];

                clipData = {
                    bH: orig.eH,
                    bM: orig.eM,
                    bS: orig.eS,
                    eH: orig.eH,
                    eM: orig.eM + 10,
                    eS: orig.eS,
                    datetime: "2020-10-12T12:34",
                    title: "hello 1 from axios",
                    source:
                        "http://127.0.0.1:8000/videos/" +
                        this.Videos[VideoIndex].id +
                        "/",
                };
            } else {
                if (this.Videos[VideoIndex].mClips.length == 0) {
                    clipData = {
                        bH: 0,
                        bM: 0,
                        bS: 0,
                        eH: 0,
                        eM: 10,
                        eS: 0,
                        datetime: "2020-10-12T12:34",
                        title: "hello 1 from axios",
                        source:
                            "http://127.0.0.1:8000/videos/" +
                            this.Videos[VideoIndex].id +
                            "/",
                    };
                } else {
                    orig = this.Videos[VideoIndex].mClips[0];
                    clipData = {
                        bH: 0,
                        bM: 0,
                        bS: 0,
                        eH: orig.bH,
                        eM: orig.bM + 10,
                        eS: orig.bS,
                        datetime: "2020-10-12T12:34",
                        title: "hello 1 from axios",
                        source:
                            "http://127.0.0.1:8000/videos/" +
                            this.Videos[VideoIndex].id +
                            "/",
                    };
                }
            }

            if (clipData.eM >= 60) {
                clipData.eM %= 60;
                clipData.eH += 1;
            }

            axios
                .post("http://localhost:8000/clips/", clipData)
                .then((res) => {
                    console.log("res!");
                    console.log(res);

                    this.allInformation();
                })
                .catch((err) => {
                    console.log(err);
                });
        },

        removeClip: function (VideoIndex, ClipIndex) {
            let cid = this.Videos[VideoIndex].mClips[ClipIndex].id;
            console.log(cid);
            axios
                .delete("http://localhost:8000/clips/" + cid + "/")
                .then((res) => {
                    console.log(res);
                    this.allInformation();
                })
                .catch((err) => {
                    console.log(err);
                });
        },

        updateClip: function (VideoIndex, ClipIndex) {
            let orig = this.Videos[VideoIndex].mClips[ClipIndex];
            let cid = orig.id;

            axios
                .put("http://localhost:8000/clips/" + cid + "/", orig)
                .then((res) => {
                    console.log(res);
                    this.allInformation();
                })
                .catch((err) => {
                    console.log(err);
                });
        },
    },

    mounted: function () {
        this.allInformation();
    },
};
</script>

<style scoped>

.stripe1 {
    background-color: mistyrose;
}

.stripe0 {
    background-color: beige;
}
</style>